.img {
    width: 100px;
    height: 100px;
}


.blockUsers {
    min-height: 600px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    border-radius: 10px;
    padding: 10px;
}

.userContainer {
    width: 230px;
    height: 230px;
    border: 2px solid #12143b;
    border-radius: 10px;
    margin: 10px;
    padding: 10px;
    box-shadow: 0px 5px 7px #2c5275;
    background-color: white;

}

.search {
    display: flex;
    margin: 10px 0;
}

button {
    padding: 5px;
}

input {
    padding: 5px;
    width: 100%;
}